<template>
  <div class="container">
    <div class="logo">Day one yo</div>
    <div class="login" v-if="!user">
      <Login/>
    </div>
    <div class="logged-in" v-else>
      Welcome back {{ user.firstName }} {{ user.lastName }}!
    </div>
    
    <Workflows />
    
    <Workflow />

  </div>
</template>

<script>
import Login from "./Login.vue";
import Workflow from "./Workflow.vue";
import Workflows from "./Workflows.vue";

export default {
  name: "HelloWorld",
  components: {
    Login,
    Workflow,
    Workflows
  },
  data: function() {
    return {
      user: null,
      workflows: null,
      workflowid: null
    };
  },
  mounted() {
    this.$root.$on("loggedIn", this.setUser);
  },
  beforeDestroy() {
    this.$root.$off("loggedIn", this.setUser);
  },
  methods: {
    setUser(user) {
      this.user = user;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "./../scss/vars";

.container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: spacing(6) auto 1px;
  grid-template-areas:
    "header-left header-right"
    "sidebar main"
    "footer footer";

  width: 100vw;
  height: 100vh;

  @include breakpoint(smallmax) {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: spacing(6) auto 1px;
    grid-template-areas:
      "header-left header-right"
      "main main"
      "footer footer";
  }
}
.logo {
  grid-area: header-left;
  justify-self: start;
  padding: spacing(1);
}

.login {
  grid-column: 1 / span 2;
  grid-row: 1 / span 3;
  z-index: zindex(modal);
  background-color: white;
  display: grid;
}

.logged-in {
  grid-area: header-right;
  justify-self: end;
  padding: spacing(1);
}

.workflow-list {
  list-style-type: none;
  margin: 0;
  padding: spacing(1);
}
</style>



