<template>
  <div class="workflow-tile" v-bind:class="[activeWorkflowID === workflow.id ? 's-active' : '']" @click="()=> this.$root.$emit('selectedWorkflowUpdated',workflow)">
      <h3>{{ workflow.process.name }}</h3>
      <div>State: {{ workflow.process.state }}</div>
      <div v-if="workflow.process.state === 'ACTIVE'">Progress: {{ workflow.stats.process.activities.states.COMPLETE || 0 }} / {{ workflow.stats.process.activities.count }} Tasks Complete</div>
  </div>
</template>

<script>
export default {
  name: "WorkflowTile",
  props: ["workflow", "activeWorkflowID"],
  data: function() {
    return {};
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.workflow-tile {
  @include containerBox;
  text-align: left;

  &:hover {
    background-color: palette(blue, 1);
  }

  &.s-active {
    background-color: palette(blue);
    color: white;
  }
}
</style>




